import React from 'react';

import $ from 'jquery';
import Foundation from 'foundation-sites';
window.jQuery = $;

class Hero extends React.Component {

    componentDidMount(){
        this.watchToggle = new Foundation.Toggler($('#panel'), {
            'toggler' : 'is-hidden'
        });
    }

	render() {
		return (
            <div id='hero' className='grid-container full'>
                <div className='grid-container'>
                    <div id='hero' className='grid-x grid-padding-x grid-padding-y align-middle'>
                        <div className='cell small-12'>
                            <p><button id='toggle' type='button' className='button success small' data-toggle='panel'>Toggle Panel</button></p>
                            <div className="callout is-hidden" id="panel">
                                <h4>Hello!</h4>
                                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Dicta quas optio alias voluptas nobis iusto mollitia asperiores incidunt reprehenderit doloribus voluptatibus officiis minus, inventore, quasi nisi. Consequuntur, quidem. Sint, dicta?</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
		);

        
	}
}

export default Hero