import React from 'react';

import $ from 'jquery';
import Foundation from 'foundation-sites';
window.jQuery = $;


const subbannersData = [
    {
        image: 'https://www.cloverimaging.com/images/page-specific/home/sub/Why-Clover-Reman.jpg',
        image_alt: '',
        title: 'Why Clover Imaging Remanufactured Printer Cartridges?',
        text: 'See our facilities and discover what makes Clover the industry leader in remanufactured imaging supplies.',
        button: 'Watch Now',
        url: '#'
    },
    {
        image: 'https://www.cloverimaging.com/images/page-specific/home/sub/719922A-Clover-HP-414X-NPA-WebThumb@2x.jpg',
        image_alt: '',
        title: 'New Product:<br/>HP 414X Color Series',
        text: 'Clover Imaging’s Remanufactured HP 414X offers unsurpassed reliability, quality, and comes with a 100% performance guarantee.',
        button: 'Order Now',
        url: '#'
    },
    {
        image: 'https://www.cloverimaging.com/images/page-specific/home/sub/lca-sub@2.jpg',
        image_alt: '',
        title: 'Clover Releases 2022 Environmental LCA',
        text: 'Read the independent study that analyzed the environmental benefits of using Clover Imaging’s remanufactured cartridges.',
        button: 'Learn More',
        url: '#'
    },
    {
        image: 'https://www.cloverimaging.com/images/page-specific/home/sub/competitive-advantage@2x.jpg',
        image_alt: '',
        title: 'Clover’s July Webinar: Using Sustainability as a Competitive Advantage',
        text: 'Watch July’s webinar and learn how to obtain a target audience with sustainability initiatives and how to meet their needs.',
        button: 'Watch The Full Webinar Here',
        url: '#'
    }
];



class SubBanners extends React.Component {
    

    
    componentDidMount(){
        this.watchSubBannersImage = new Foundation.Equalizer($('#subbanners'), {
            'data-equalize-by-row': true,
            'data-equalize-on-stack': true
        });
        this.watchSubBannersTitle = new Foundation.Equalizer($('#subbanners .grid-container'), {
            'data-equalize-by-row': true,
            'data-equalize-on-stack': true
        });
        this.watchSubBannersText = new Foundation.Equalizer($('#subbanners .equal-items'), {
            'data-equalize-by-row': true,
            'data-equalize-on-stack': true
        });
    }

	render() {
		return (
            <div id='subbanners' className='grid-container full' data-equalizer='image' >
                <div className='grid-container' data-equalizer='title'>
                    <div className='equal-items grid-x grid-padding-x grid-padding-y' data-equalizer='text' >
                        {subbannersData.map((item, idx) => this.addToSubWrap(item, idx))}
                    </div>
                </div>
            </div>
		);
	}

    addToSubWrap(item, idx) {
		return (
            <div className='cell medium-6 large-3' key={idx} >
                <div className='item'>
                    <div className='image' data-equalizer-watch='image'>
                        <img src={item.image} alt={item.image_alt} />
                    </div>
                    <div className='title' data-equalizer-watch='title'>
                        <h6 dangerouslySetInnerHTML={ { __html:item.title }} />
                    </div>
                    <div className='text' data-equalizer-watch='text'>
                        <p>{item.text}</p>
                    </div>
                    <a href={item.url}>{item.button}</a>
                </div>
            </div>
        )
	}
}

export default SubBanners