import React from 'react';
import { Link } from 'react-router-dom';
import Clock from '../Clock/Clock';
import logo from "./../../assets/images/shared/topbar-logo.svg";

class TopBar extends React.Component {
	// props
	// constructor(props) {
	// 	super(props);
	// 	this.state = {
	// 		menuItems: menuItems
	// 	};
	// }

	// static defaultProps = {
    //     name: menuItems
    // }

	// state on mount
	// componentDidMount() {}

	// on unmount
	// componentWillUnmount() {}

	// render
	render() {
		return (
			<div id='top-bar' className='grid-container full'>
				<div className='grid-container'>
					<div className='grid-x grid-padding-x grid-padding-y align-middle'>
						<div className='cell medium-3 large-2'>
							<Link to="/">
								<img className='logo' src={logo} alt='Clover Imaging Logo'/>
							</Link>
						</div>
						<div className='cell medium-9 large-10'>
							<div className='grid-x align-middle'>
								<div className='cell large-7'>
									<Link className='button small warning mb-0' to="/blog">Dashboard</Link>
								</div>
								<div className="cell large-5 text-right">
									<Link className='button small warning mb-0' to="/dashboard">Dashboard</Link>
									<Clock />
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}




export default TopBar